// 
@mixin for_mobile {
  @media (min-width: $breakpoint_mobile) {
    @content;
  }
}

@mixin for_tablet {
  @media (min-width: $breakpoint_tablet) {
    @content;
  }
}

@mixin for_desktop {
  @media (min-width: $breakpoint_desktop) {
    @content;
  }
}

@mixin for_retina {
  @media (min-resolution: $retina_dppx), (min-resolution: $retina_dpi) {
    @content;
  }
}

//   
@mixin font_character($size, $height, $weight, $color) {
  font-size: $size;
  line-height: $height;
  font-weight: $weight;
  color: $color;
}

//    
@mixin list_default_remove {
  list-style: none;
  padding: 0;
  margin: 0;
}

//       <a>
@mixin link_default_remove {
  text-decoration: none;
  color: inherit;
  outline: none;
}

//     
@mixin only_mobile {
  @media (min-width: $breakpoint_tablet) {
    display: none;
  }
}

//     
@mixin only_tablet {
  @media (max-width: $breakpoint_tablet - 1), (min-width: $breakpoint_desktop) {
    display: none;
  }
}

//     
@mixin only_desktop {
  @media (max-width: $breakpoint_desktop - 1) {
    display: none;
  }
}
